.hero {
  min-height: 40vh;
  display: -ms-grid;
  display: grid;
  place-items: center;
}

.hero .img-metrics {
  display: none;
}

.hero h1, .hero p {
  margin: 0;
}

.hero h1 {
  font-size: 2.25rem;
  color: #164a41;
}

.hero p {
  font-size: 1.125rem;
}

@media (min-width: 768px) {
  .hero {
    height: calc(100vh - 15rem);
    -ms-grid-columns: 1fr 1fr;
        grid-template-columns: 1fr 1fr;
  }
  .hero .img-metrics {
    display: block;
    width: 80%;
