.map{
    margin-top: 2rem;

    &-title{
        font-size: $fs-h3;
        text-align: center; 
        color: $clr-dark; 
    }

    &-icons{
        color: $clr-dark;
        cursor: pointer;
        svg {
            font-size: 2rem;
          }
    }

    &-farm-card{
        width: 200px;
        height: auto;
        h4{
            color: $clr-dark;
            text-transform: uppercase;
        }
        p{
            font-size: $fs-subtitle;
            margin-top: 0;
            margin-bottom: .5rem;
        }
    }
}