.hero{    
  min-height: 40vh;
  display: grid;
  place-items: center;
    
  .img-metrics{
      display: none;
  }
  
  h1,p{margin: 0;}
  h1 { 
      font-size: $fs-h2;
      color: $clr-dark;    
  }
  p {font-size: $fs-h4;}
}

@media (min-width: $medium-width) {
  .hero{
    height: calc(100vh - 15rem);
    grid-template-columns: 1fr 1fr;

    .img-metrics{
      display: block;
      width: 80%;
      object-fit: cover; 

    }
  }
  
  h1 {font-size: $fs-h1;}
  h2 {font-size: $fs-h2;}
}

.services {
    margin-top: 2rem;
    display: grid;
    gap: 2.5rem;
    
    .service-card {
        background: lighten($clr-dark, 5%);
        text-align: center;
        padding: 2rem 1.5rem;
        border-radius: $radius;
        h4{
          font-size: $fs-h4;
          color: $clr-light;
        }

        p {
          color: $clr-light;
          font-weight: $fw-light;
          font-size: $fs-subtitle;
        }

        .icon{
          width: 4.5rem;
          height: 4.5rem;
          display: grid;
          margin: 0 auto;
          place-items: center;
          margin-bottom: 1rem;
          border-radius: 50%;
          background: $clr-accent;
          color: $clr-light;
          svg {
            font-size: 2.5rem;
          }
        }
      }

}

@media (min-width: $min-width) {
    .services{
      grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    }
  }