@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap');

/* font variables */
  $ff-primary: 'Roboto', sans-serif;
  $fw-light:300;
  $fw-reg: 400;
  $fw-bold: 700;

  $fs-h1: 2.75rem;
  $fs-h2: 2.25rem;
  $fs-h3: 1.75rem;
  $fs-h4: 1.125rem;
  $fs-body: 1rem;
  $fs-subtitle: .925rem;

  /* Color variables */
  $clr-light: #fff;
  $clr-dark: #164a41;
  $clr-grey: rgb(70, 69, 69);
  $clr-light-grey: #ddd;
  $clr-accent:#F1b24a; 

  /* other styles */
  $transition: all 0.3s linear;
  $spacing: 0.1rem;
  $radius: 0.25rem;

  // Size of browsers
  $max-width: 992px;
  $medium-width: 768px; 
  $min-width: 400px;
