.farms-header{
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-bottom: 2rem;
    margin-top: 1rem;
    padding-bottom: .5rem;

    &-title {
        font-size: $fs-h2;
        font-weight: $fw-bold;
        margin: 0;
        text-transform: uppercase;
        text-align: center;
        color: $clr-dark;
    }

    &-subtitle{
        display: none;
    }
    ul{
        display: flex;
        justify-content: center;
      }

    li{
        list-style-type: none;
        margin-left: 1rem;
        font-size: $fs-subtitle;
      }
    
    p, a{
        color: $clr-grey;
        font-weight: 300;
      }
    a {
        text-decoration: none;
    }
    a:hover,
    a:focus{
        color: $clr-dark;
        font-weight: $fw-bold;
    }
}

@media (min-width: $medium-width) {
    .farms-header{
        flex-direction: row;
        justify-content: space-between;

        &-title{
            font-size: $fs-h1;
            text-align: left;
        }

        &-subtitle{
            display: block;
            letter-spacing: 3px;
            margin-top: 0;
            font-size: $fs-subtitle;
        }

        ul{
            justify-content: space-between;
            align-items: flex-end;
          }
    }

}