.farms-filter{
    position: sticky;
    width: 100%;
    padding-right: .5rem;
    margin-top: 2.5rem;
    .farms-filter-form{
        margin-bottom: 1.25rem;
        h5 {
          margin-bottom: 0.5rem;
          font-size: $fs-h4;
        }
      }
      .search-input {
        width: 100%;
        padding: 0.5rem;
        background: $clr-light-grey;
        border-radius: $radius;
        border-color: transparent;
        letter-spacing: $spacing;
        font-family: $ff-primary;
      }
      .search-input::placeholder {
        text-transform: capitalize;
      }

      .farms-filter-form-datetime{
        display: flex;
        justify-content: space-between;
      }

      .select-input {
        background: $clr-light-grey;
        border-radius: $radius;
        border-color: transparent;
        padding: 0.5rem;
      }
      .farm{
        width: 100%;
      }

      button {
        display: block;
        margin: 0.25em 0;
        padding: 0.25rem 0;
        
        background: transparent;
        border: none;
        border-bottom: 1px solid transparent;
        letter-spacing: $spacing;
        color: $clr-dark;
        cursor: pointer;
      }
      .active {
        border-color: $clr-dark;
      }
      .btn-clear{
        background: $clr-dark;
        color: $clr-light;
        padding: 0.5rem;
        border-radius: $radius;
      }
}