@import 'variables' ;
@import 'header' ;
@import 'hero';
@import 'map';
@import 'statistics';
@import 'filter';
@import 'footer' ;

*{
    box-sizing: border-box;
}

body{
    margin: 0;
    font-size: $fs-body;
    font-family: $ff-primary;
    background-color: $clr-light;
}

.app-container{
    width: 100%;
    min-width: $min-width;
    margin: 0 auto;
    padding: .5rem;

}

@media (min-width: $medium-width) {
    .app-container{
        max-width: $max-width;
    }
}

.loading{
    color: $clr-dark;
    text-align: center;
}