.farms-statistics{
    display: grid;
}

.pagination-btn-container {
    display: flex;
    justify-content: center;   
    flex-wrap: wrap;
    font-size: $fs-body;
    margin-top: .5rem;
  }
  .pagination-choose-page{
    display: flex;
    justify-content: center;
    justify-items:center;
    font-weight: $fw-light;
    margin-top: .5rem;
    margin-bottom: 1rem;
    font-size: $fs-subtitle;
    p{
      margin: 0;
      padding: .3rem 1rem .3rem .5rem;

    }
    input{
      width: 50px;
      padding: .3rem auto .3rem .5rem;
      margin-left: .5rem;
      font-size: $fs-subtitle;
    }
    .btn{     
      border-radius: 5px;
      background: $clr-dark;
      color: $clr-light;
      border: 0;
      margin-left: .5rem;
      padding: .3rem .5rem;
      font-size: $fs-subtitle;
      letter-spacing: $spacing;
      cursor: pointer;
    }
  }
  
.pagination-btn-container {  
  .btn-page {
    width: 2.5rem;
    height: 2rem;
    background: $clr-light;
    color: $clr-dark;
    border-color: transparent;
    border-radius: 5px;
    cursor: pointer;
    margin: 0.1rem;
    transition: $transition;
  }
  .btn-active {
    background: $clr-dark;
    color: $clr-light;
  }
  .btn-prev,
  .btn-next {
    height: 2rem;
    background: transparent;
    border-color: transparent;
    margin: 0.1rem;
    cursor: pointer;
  }
}
#farms-statistics-table{
    width: 100%;
    text-align: left;
    padding-top: .2em;

    th, td{
        border-bottom: 1px solid $clr-light-grey;
        padding: .5em;
    }

    th{
        background-color: $clr-dark; 
        color: $clr-light;
        letter-spacing: $spacing;
        font-weight: $fw-light;
        text-transform: uppercase;
    }
    td{
        font-weight: $fw-light;
        font-size:  $fs-subtitle;
    }
    tr:nth-child(even){background-color: #f2f2f2;}
    tr:hover {background-color: $clr-light-grey;}

}

@media (min-width: $medium-width) {
  .farms-statistics{
    width: 100%;
    grid-template-columns: 300px auto;
  }
}